.float{
	position:fixed;
	width:60px;
	height:60px;
	bottom:40px;
	right:40px;
	background-color:black;
	color:rgb(17, 17, 17);
	border-radius:50px;
	text-align:center;
  font-size:30px;
	box-shadow: 2px 2px 3px rgba(0, 0, 0, 1);
  z-index:900;
}
.float:hover {
	text-decoration: none;
	/* color: #02085f; */
  /* background-color:rgb(26, 25, 25); */
}

