/*css for manual slide navigation*/

.navigation-manual{
position: absolute;
width: 100%;
text-align: center;
bottom: 10px;
left: 0;
}

.manual-btn{
  border: 2px solid rosybrown;
  padding: 5px;
  /* width: 4px;
  height: 4px; */
  border-radius: 50%;
  cursor: pointer;
  transition: 1s;
  display: inline-block;
}

.manual-btn:not(:last-child){
  margin-right: 40px;
}

.manual-btn:hover{
  background: rosybrown;
}

#radio1:checked ~ .first{
  margin-left: -0%;
}

#radio2:checked ~ .first{
  margin-left: -20%;
}

#radio3:checked ~ .first{
  margin-left: -40%;
}

