 .button{
  /* display: block; */
  width: 35%;
  height: 50px;
  font-size: 18px;
  text-align: center;
  /* outline: none; */
  /* border: 1px solid rgb(0, 141, 235); */
  border: 1px solid  #2c2c3dec;
  background-color: #fff;
  border-radius: 4px;
  color: #2c2c3dec;
  cursor: pointer;
  transition: all .25s;
  z-index: 1;
  position: relative;
  padding-left: 4px;
  padding-right: 4px;
  
}

.button::before{
  content: '';
  position: absolute;
  width: 100%;
  height: 0;
  left: 0;
  bottom: 0;
  border: transparent;
  border-radius: 2px;
  /* background-image: linear-gradient(to right, 
  rgb(23,228,243), rgb(0,141,235)); */
  background: linear-gradient(90deg, #2c2c3d 50%, #c03535 100%);


  transition: all .25s;
  z-index: -1;
}

.button:hover{
  border:1px solid transparent;
  color: #fff;
}

.button:hover::before{
  height: 100%;
} 

